export const alignmentMocks = [
  { name: AlignmentEnum.CaoticoBondoso, description: 'So espritos livres que promovem o bem, mas preferem seguir seus prprios instintos e convices, em vez de confiar em regras. No acham errado mentir, trapacear e roubar para trazer bem-estar e alegria a outros. Tambm se preocupam em proteger a liberdade alheia, muitas vezes combatendo tiranos. Diante de uma criana roubando po, ajuda a encobrir a fuga da criana. Pode at orient-la a roubar de comerciantes ricos e inescrupulosos e tambm dividir seu roubo com outros famintos.' },
  { name: AlignmentEnum.CaoticoMaligno, description: 'Diferente de algum que apenas no se importa com outros, estes monstros so verdadeiramente cruis, tirando prazer do sofrimento alheio. Torturam e matam por diverso ou necessidade perversa. So brutais, violentos e imprevisveis, capazes de qualquer coisa. Por todos estes motivos,  quase impossvel que consigam viver em sociedade  voc no encontra um destes andando pelas ruas com frequncia. Tm dificuldade em fazer planos e s trabalham em equipe quando obrigados por fora ou intimidao. Mataria a criana. E o mercador. E quem mais estivesse por perto. E comeria, com po.' },
  { name: AlignmentEnum.CaoticoNeutro, description: 'Fazem o que bem entendem, quando bem entendem, sem se importar com o que outros pensam. Valorizam a prpria liberdade, mas sem preocupao ou respeito pela liberdade dos outros. Embora no aceitem autoridade, tambm no lutaro contra ela, exceto quando essa autoridade tenta impedi-los de fazer algo. So impacientes e imprevisveis, at parecem loucos, mas quase nunca decidem fazer algo que traga prejuzo ou dano para si mesmos. Diante de uma criana roubando po, faz o que parecer mais divertido. Talvez ajude na fuga da criana ou aproveite a distrao para pegar seu prprio pedao.' },
  { name: AlignmentEnum.LealBondoso, description: 'Estas pessoas fazem o que  esperado de uma pessoa justa, respeitando a lei e sacrificando-se para ajudar os necessitados. Cumprem suas promessas e dizem a verdade. Quando no assumem a liderana, so fiis e devotados a seus comandantes. So intolerantes com o mal; mesmo sendo capazes de perdo e compaixo, acreditam que todo crime precisa ser punido. Este  o alinhamento dos paladinos. Diante de uma criana faminta roubando um pedao de po, vai explicar que roubar  errado, comprar comida para ela e sua famlia, ento lev-la at um guarda da milcia.' },
  { name: AlignmentEnum.LealMaligno, description: 'Estes viles acreditam que ordem, tradies e cdigos de conduta so mais importantes que liberdade, dignidade e a vida  especialmente de outros.Podem estar presos a tabus, cdigos de honra, devoes a deuses ou temor por maldies.Seguem leis pessoais ou impostas por lderes, sentindo- se seguros e confortveis ao faz - lo, mesmo causando sofrimento alheio.So metdicos e organizados. Muitos respeitam regras de combate e cumprem a palavra, mesmo frente a heris inimigos. Diante da criana faminta roubando po, trataria de castigar o pequeno ladro ali mesmo ou entreg - lo  milcia para receber a punio mais severa.' },
  { name: AlignmentEnum.LealNeutro, description: 'Pessoas metdicas e disciplinadas, que obedecem s leis e cumprem suas promessas a qualquer custo  pouco importando quem  beneficiado ou prejudicado. Alguns adotam uma disciplina mais pessoal, enquanto outros tentam impor suas normas a todos ao redor. Sua sinceridade pode ser dura; dizem o que pensam e no mentem, mesmo quando a verdade pode magoar ou prejudicar algum. Diante de uma criana faminta roubando po, vai impedir o roubo e avisar a milcia ou levar a criana s autoridades.' },
  { name: AlignmentEnum.Neutro, description: 'Indivduos com este alinhamento costumam ser indiferentes, fracos em suas convices, sem grandes preocupaes morais ou ticas. Ou ento lutam ativamente pelo equilbrio entre bem, mal, lei e caos, j que um no existe sem o outro. Usam simples bom senso para tomar suas decises e no geral fazem aquilo que parece ser uma boa ideia. Preferem estar com pessoas leais e bondosas;  sempre melhor viver com gente sincera e generosa, em vez de algum que tenta tirar vantagem de voc. Diante da criana que rouba po, uma pessoa verdadeiramente neutra em geral no se envolve, a menos que tenha alguma ligao pessoal com a criana ou o mercador. Certas criaturas  incluindo os animais  no possuem nenhuma moral ou tica. No sabem a diferena entre certo e errado. Agem seguindo seu instinto ou programao, sem a capacidade de fazer escolhas reais. Um gorlogg e um colosso da Supremacia podem ser perigosos, mas no malignos; apenas fazem aquilo que foram criados para fazer. Estas criaturas tambm so consideradas neutras.' },
  { name: AlignmentEnum.NeutroBondoso, description: 'So pessoas de bom corao, que sentem prazer e satisfao pessoal com a felicidade de outros. Colaboram com as autoridades, mas no se sentem obrigadas a faz-lo  acham que ajudar o prximo  mais importante que seguir ordens ou leis. Diante de uma criana faminta roubando um pedao de po, ajuda tanto a criana quanto o mercador roubado. No tentar punir a criana (talvez apenas dar-lhe um bom susto).' },
  { name: AlignmentEnum.NeutroMaligno, description: 'So egostas e mesquinhos, colocando a si mesmos sempre em primeiro lugar. Pegam o que querem, pouco importando quem precisam roubar ou matar. Quando fazem algum tipo de aliana,  apenas para tirar vantagem do parceiro e tra-lo no momento oportuno. Adotam regras para quebr-las em seu prprio benefcio no minuto seguinte. Diante da criana roubando po, ameaa entreg-la  milcia se no obedecer s suas ordens. Pode at chantagear seus pais, tomando tudo que possuem.' },
]

